:root {
  --page-bg: #000000;
  --card: #0f1116;
  --muted: #9aa3b2;
  --primary: #6c63ff;
  --ring: rgba(108, 99, 255, 0.2);
  --gold: rgba(255, 215, 130, 0.14);
  --gold-strong: rgba(255, 215, 130, 0.26);
}

html, body, #root { height: 100%; }

body {
  margin: 0;
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, 'Helvetica Neue', 'Noto Sans', 'Segoe UI Emoji', 'Apple Color Emoji', Arial, sans-serif;
  color: #ffffff;
  background: radial-gradient(1200px 800px at 80% -10%, var(--ring), transparent 60%) var(--page-bg);
  scroll-snap-type: y proximity;
}

.nav {
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 24px;
  background: linear-gradient(180deg, rgba(0,0,0,0.9), rgba(0,0,0,0));
  backdrop-filter: saturate(140%) blur(6px);
}

.nav .logo { font-weight: 800; letter-spacing: 0.5px; }

.hero-spacer { height: 60vh; display: grid; place-items: center; }

.stack { position: relative; width: min(1120px, 92vw); margin: 0 auto; }

.slide {
  position: sticky;
  top: 0;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(20px, 3vw, 48px);
  align-items: center;
  padding: clamp(16px, 3vw, 32px);
  border-radius: 24px;
  /* Layered background base: background image + dotted grid + deep black; golden streaks come from ::before */
  background:
    radial-gradient(#ffffff12 1px, transparent 1px) 0 0 / 24px 24px,
    url('./Slide.bg.png') center/cover no-repeat,
    #000000;
  box-shadow: 0 10px 40px rgba(0,0,0,0.6);
  border: 1px solid rgba(128, 128, 128, 0.3);
  z-index: 0;
  scroll-snap-align: start;
  transition: grid-template-columns 260ms ease;
  overflow: hidden;
}

/* Blurry golden slanted light overlay */
.slide::before {
  content: "";
  position: absolute;
  inset: -10% -20%; /* extend outside for nicer blur falloff */
  background:
    /* subtle global glow */
    radial-gradient(1200px 800px at 80% -10%, var(--ring), transparent 60%),
    /* five smudged golden streaks */
    linear-gradient(115deg,
      rgba(255, 215, 130, 0) 0%,
      rgba(255, 215, 130, 0) 30%,
      rgba(255, 215, 130, 0.22) 33%,
      rgba(255, 215, 130, 0) 36%,
      rgba(255, 215, 130, 0) 100%
    ),
    linear-gradient(115deg,
      rgba(255, 215, 130, 0) 0%,
      rgba(255, 215, 130, 0) 16%,
      rgba(255, 215, 130, 0.32) 20%,
      rgba(255, 215, 130, 0) 24%,
      rgba(255, 215, 130, 0) 100%
    ),
    linear-gradient(115deg,
      rgba(255, 215, 130, 0) 0%,
      rgba(255, 215, 130, 0) 46%,
      rgba(255, 215, 130, 0.28) 50%,
      rgba(255, 215, 130, 0) 54%,
      rgba(255, 215, 130, 0) 100%
    ),
    linear-gradient(115deg,
      rgba(255, 215, 130, 0) 0%,
      rgba(255, 215, 130, 0) 62%,
      rgba(255, 215, 130, 0.24) 65%,
      rgba(255, 215, 130, 0) 68%,
      rgba(255, 215, 130, 0) 100%
    ),
    linear-gradient(115deg,
      rgba(255, 215, 130, 0) 0%,
      rgba(255, 215, 130, 0) 76%,
      rgba(255, 215, 130, 0.26) 80%,
      rgba(255, 215, 130, 0) 84%,
      rgba(255, 215, 130, 0) 100%
    );
  filter: blur(18px) saturate(112%);
  opacity: 0.42;
  mix-blend-mode: screen;
  pointer-events: none;
  z-index: 0;
}

/* Ensure content sits above the blurred overlay */
.slide > * { position: relative; z-index: 1; }

.slide + .slide { margin-top: -12vh; }
.slide:nth-child(1) { z-index: 1; }
.slide:nth-child(2) { z-index: 2; }
.slide:nth-child(3) { z-index: 3; }
.slide:nth-child(4) { z-index: 4; }
.slide:nth-child(5) { z-index: 5; }

.slide .copy h3 {
  margin: 0 0 8px;
  color: var(--primary);
  font-weight: 800;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-size: 12px;
}

.slide .copy h2 { margin: 4px 0 10px; font-size: clamp(32px, 5vw, 58px); line-height: 1.02; }
.slide .copy p { margin: 0; color: var(--muted); font-size: clamp(14px, 2vw, 18px); }

.card {
  width: 100%;
  /* Match card size to media size: maintain a consistent 16:9 frame that
     scales responsively with the layout while capping height for viewport */
  aspect-ratio: 16 / 9;
  max-height: 80vh;
  border-radius: 16px;
  background: #000000; /* solid black card */
  border: 1px solid rgba(255,255,255,0.06);
  box-shadow: 0 10px 30px rgba(0,0,0,0.7);
  position: relative;
  overflow: hidden; /* Clip video to rounded corners */
  transition: transform 220ms ease, box-shadow 220ms ease;
  transform-origin: center;
  cursor: none; /* hide system cursor on card */
}

/* For cards with video wrapper, allow button to show */
.card:has(.video-wrapper) {
  overflow: visible;
}

.card:has(.video-wrapper) .pointer {
  overflow: hidden;
  border-radius: 16px;
}
.card:hover { transform: scale(1.06); box-shadow: 0 24px 70px rgba(0,0,0,0.9), inset 0 1px 0 rgba(255,255,255,0.08); }

/* Expand only when the media card is hovered; text hover keeps equal widths */
.slide:has(.card:hover) { grid-template-columns: 0.6fr 1.4fr; }
.slide:has(.copy:hover) { grid-template-columns: 1fr 1fr; }

.pointer { position: absolute; inset: 0; display: grid; place-items: center; color: inherit; font-weight: 700; font-size: 18px; }

/* Custom cursor: purple rounded pointer + You pill */
.card .cursor-tag {
  position: absolute;
  left: var(--mx, -1000px);
  top: var(--my, -1000px);
  transform: translate(12px, 8px);
  background: #6c63ff;
  color: #ffffff;
  padding: 6px 12px;
  border-radius: 999px;
  box-shadow: 0 0 0 2px #ffffff; /* white border */
  font-weight: 800;
  font-size: 12px;
  line-height: 1;
  pointer-events: none;
  opacity: 0;
  transition: opacity 120ms ease;
  z-index: 3;
}
.card:hover .cursor-tag, .card.is-hover .cursor-tag { opacity: 1; }
.card * { cursor: none; }

/* Rounded purple pointer (foreground) */
.card .cursor-tag::before {
  content: "";
  position: absolute;
  left: -30px;
  top: -6px;
  width: 28px;
  height: 24px;
  background: no-repeat center/100% 100% url("data:image/svg+xml;utf8,<?xml version='1.0' encoding='UTF-8'?><svg xmlns='http://www.w3.org/2000/svg' width='28' height='24' viewBox='0 0 28 24'>\n  <path d='M3 2.5 L22 10.5 L12.2 12.8 L8.2 21 L7 12.4 Z' fill='%236c63ff' stroke='%23ffffff' stroke-width='3' stroke-linejoin='round' stroke-linecap='round'/></svg>");
  z-index: 2;
}

/* White outline under the pointer (slightly larger) */
.card .cursor-tag::after {
  content: "";
  position: absolute;
  display: none; /* outline handled by stroke in ::before */
}

/* Make embedded media (like the V1 video) fill the card neatly */
.card .media {
  width: 100%;
  height: 100%;
  object-fit: contain; /* keep full video visible without cropping */
  object-position: center center;
  background: #000;
  border-radius: 16px;
}

/* Video wrapper for voice toggle */
.video-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  overflow: hidden;
}

/* Voice toggle button - positioned at right end of video */
.voice-toggle-button {
  position: absolute;
  bottom: 16px;
  right: 16px;
  padding: 8px;
  background: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 220ms ease;
  z-index: 10;
}

.voice-toggle-button:hover {
  opacity: 0.8;
  transform: scale(1.1);
}

.voice-toggle-button:active {
  transform: scale(0.95);
}

.voice-toggle-button svg {
  width: 24px;
  height: 24px;
}

.reveal { opacity: 0; transform: translateY(16px); transition: opacity 500ms ease, transform 600ms cubic-bezier(.2,.65,.2,1); }
.reveal.in { opacity: 1; transform: translateY(0); }

@media (max-width: 900px) {
  .slide { grid-template-columns: 1fr; min-height: 100vh; }
  .slide + .slide { margin-top: -10vh; }
  .slide:has(.card:hover), .slide:has(.copy:hover) { grid-template-columns: 1fr; }
}



